import {
  emojiMap,
  emojiUrl
} from './emojiMap';
/** message.elementSystemMessageGroupTip
 * content = {
 *  type: 'TIMTextElem',
 *  content: {
 *    text: 'AAA[]AAA[]AAA[AAA]'
 *  }
 *}
 **/
//  (opType)

const GROUP_TIP_TYPE = {
  MEMBER_JOIN: 1,
  MEMBER_QUIT: 2,
  MEMBER_KICKED_OUT: 3,
  MEMBER_SET_ADMIN: 4,
  // 
  MEMBER_CANCELED_ADMIN: 5,
  // 
  GROUP_INFO_MODIFIED: 6,
  // msgBody.msgGroupNewInfo.ownerAccountID
  MEMBER_INFO_MODIFIED: 7 // 

}; // text, []

export function parseText(message) {
  const renderDom = [];
  let temp = message.payload.text;
  let left = -1;
  let right = -1;

  while (temp !== '') {
    left = temp.indexOf('[');
    right = temp.indexOf(']');

    switch (left) {
      case 0:
        if (right === -1) {
          renderDom.push({
            name: 'span',
            text: temp
          });
          temp = '';
        } else {
          const _emoji = temp.slice(0, right + 1);

          if (emojiMap[_emoji]) {
            renderDom.push({
              name: 'img',
              src: emojiUrl + emojiMap[_emoji]
            });
            temp = temp.substring(right + 1);
          } else {
            renderDom.push({
              name: 'span',
              text: '['
            });
            temp = temp.slice(1);
          }
        }

        break;

      case -1:
        renderDom.push({
          name: 'span',
          text: temp
        });
        temp = '';
        break;

      default:
        renderDom.push({
          name: 'span',
          text: temp.slice(0, left)
        });
        temp = temp.substring(left);
        break;
    }
  }

  return renderDom;
} // 

export function parseGroupSystemNotice(message) {
  const {
    payload
  } = message;
  const groupName = payload.groupProfile.name || payload.groupProfile.groupID;
  const {
    groupID
  } = payload.groupProfile;
  let text;

  switch (payload.operationType) {
    case 1:
      text = `${payload.operatorID} ${groupName}ID:${groupID})`;
      break;

    case 2:
      text = `${groupName} ID:${groupID})`;
      break;

    case 3:
      text = `${groupName} ID:${groupID})`;
      break;

    case 4:
      text = `${payload.operatorID}${groupName}ID:${groupID})`;
      break;

    case 5:
      text = `${groupName} ID:${groupID})${payload.operatorID}`;
      break;

    case 6:
      text = `ID:${payload.operatorID}:${groupName}ID:${groupID})`;
      break;

    case 7:
      text = `ID${payload.operatorID}${groupName}ID:${groupID})`;
      break;

    case 8:
      text = `${groupName}ID:${groupID})`;
      break;

    case 9:
      text = `${payload.operatorID}${groupName}ID:${groupID})`;
      break;

    case 10:
      text = `${payload.operatorID}${groupName} ID:${groupID})`;
      break;

    case 255:
      text = `: ${payload.userDefinedField}`;
      break;
  }

  return text;
} // 

export function parseGroupTip(message) {
  const {
    payload
  } = message;
  const userName = message.nick || payload.userIDList.join(',');
  let tip;
  let user;

  switch (payload.operationType) {
    case GROUP_TIP_TYPE.MEMBER_JOIN:
      tip = `${userName} `;
      break;

    case GROUP_TIP_TYPE.MEMBER_QUIT:
      tip = `${userName}`;
      break;

    case GROUP_TIP_TYPE.MEMBER_KICKED_OUT:
      tip = `${userName}`;
      break;

    case GROUP_TIP_TYPE.MEMBER_SET_ADMIN:
      tip = `${payload.operatorID} ${userName}`;
      break;

    case GROUP_TIP_TYPE.MEMBER_CANCELED_ADMIN:
      tip = `${payload.operatorID} ${userName}`;
      break;

    case GROUP_TIP_TYPE.GROUP_INFO_MODIFIED:
      tip = '';
      break;

    case GROUP_TIP_TYPE.MEMBER_INFO_MODIFIED:
      for (const member of payload.memberList) {
        if (member.muteTime > 0) {
          tip = `${member.userID}${member.muteTime}`;
        } else {
          tip = `${member.userID}`;
        }
      }

      break;

    case 256:
      user = message.nick || message.from;

      if (payload.text === '') {
        user = payload.userIDList.join(',');
      }

      tip = payload.text === '' ? '' : `"${user}" ${payload.text}`;
      break;
  }

  return [{
    name: 'groupTip',
    text: tip
  }];
} // 

export function parseImage(message) {
  const renderDom = [{
    name: 'image',
    //  1080P 
    src: message.payload.imageInfoArray[0].url
  }];
  return renderDom;
} // 

export function parseVideo(message) {
  const renderDom = {
    name: 'video',
    src: message.payload.videoUrl
  };
  return renderDom;
} // 

export function parseAudio(message) {
  const renderDom = {
    name: 'audio',
    src: message.payload.url,
    second: message.payload.second === 0 ? 1 : message.payload.second
  };
  return renderDom;
}
